import { Component, Input } from '@angular/core';
import { LoadMoreMethod } from '@backbase/payment-orders-ang';
import { StopChecksListPropertiesService } from './services/stop-checks-list-properties.service';
import { StopChecksListService } from './services/stop-checks-list.service';

@Component({
  selector: 'bb-payord-stop-checks-list-widget-ang',
  template: ` <bb-stop-checks-list-container></bb-stop-checks-list-container> `,
  providers: [StopChecksListService, StopChecksListPropertiesService],
})
export class PayordStopChecksListWidgetAngComponent {
  /**
   * Number of payments per page
   */
  @Input() set pageSize(value: number) {
    this.preferences.setPageSize(value);
  }

  /**
   * Max. number of navigation pages
   */
  @Input() set maxNavPages(value: number) {
    this.preferences.setMaxNavPages(value);
  }

  /**
   * Number of payments per page
   */
  @Input() set paginationType(value: LoadMoreMethod) {
    this.preferences.setPaginationType(value);
  }

  constructor(private readonly preferences: StopChecksListPropertiesService) {}
}
