import { Injectable, Optional } from '@angular/core';
import { BehaviorSubject, combineLatest, Observable, of } from 'rxjs';
import { ItemModel } from '@backbase/foundation-ang/core';
import { map } from 'rxjs/operators';
import { defaultChecksParams } from '../helpers/constants';
import { LoadMoreMethod, toInteger } from '@backbase/payment-orders-ang';

@Injectable()
export class StopChecksListPropertiesService {
  private readonly pageSizeSubject$ = new BehaviorSubject<number | undefined>(
    undefined
  );
  private readonly maxNavPagesSubject$ = new BehaviorSubject<
    number | undefined
  >(undefined);
  private readonly paginationTypeSubject$ = new BehaviorSubject<
    LoadMoreMethod | undefined
  >(undefined);

  private get pageSizeIM(): Observable<number> {
    return (
      this.itemModel
        ?.property('pageSize', defaultChecksParams.size)
        .pipe(map(toInteger)) || of(defaultChecksParams.size)
    );
  }

  private get maxNavPagesIM(): Observable<number> {
    return (
      this.itemModel
        ?.property('maxNavPages', defaultChecksParams.maxNavPages)
        .pipe(map(toInteger)) || of(defaultChecksParams.maxNavPages)
    );
  }

  private get paginationTypeIM(): Observable<LoadMoreMethod> {
    return (
      this.itemModel?.property(
        'paginationType',
        defaultChecksParams.paginationType
      ) || of(defaultChecksParams.paginationType)
    );
  }

  readonly pageSize$ = combineLatest([
    this.pageSizeSubject$,
    this.pageSizeIM,
  ]).pipe(map(([value, valueIM]) => value ?? valueIM));

  readonly maxNavPages$ = combineLatest([
    this.maxNavPagesSubject$,
    this.maxNavPagesIM,
  ]).pipe(map(([value, valueIM]) => value ?? valueIM));

  readonly paginationType$ = combineLatest([
    this.paginationTypeSubject$,
    this.paginationTypeIM,
  ]).pipe(map(([value, valueIM]) => value ?? valueIM));

  setPageSize(value: number) {
    this.pageSizeSubject$.next(value);
  }

  setMaxNavPages(value: number) {
    this.maxNavPagesSubject$.next(value);
  }

  setPaginationType(value: LoadMoreMethod) {
    this.paginationTypeSubject$.next(value);
  }

  constructor(@Optional() private readonly itemModel: ItemModel) {}
}
