import { Injectable } from '@angular/core';
import {
  BehaviorSubject,
  combineLatest,
  EMPTY,
  Observable,
  Subject,
} from 'rxjs';
import {
  catchError,
  finalize,
  map,
  scan,
  shareReplay,
  startWith,
  switchMap,
  tap,
  withLatestFrom,
} from 'rxjs/operators';
import { StopChecksListPropertiesService } from './stop-checks-list-properties.service';
import {
  StopChecksGetResponse,
  StopChecksHttpService,
  GetStopChecksRequestParams,
} from '@backbase/data-ang/stop-checks';
import { HttpErrorResponse } from '@angular/common/http';
import {
  fromResponse,
  LoadMoreMethod,
  StopCheck,
} from '@backbase/payment-orders-ang';

@Injectable()
export class StopChecksListService {
  private readonly currentPage$ = new BehaviorSubject<number>(0);
  private readonly params$ = combineLatest([
    this.currentPage$,
    this.preferences.pageSize$,
  ]).pipe(
    map(([currentPage, size]) => {
      const from = currentPage * size;
      return { from, size };
    })
  );

  private readonly stopChecksData$ = this.params$.pipe(
    tap(() => this.loading$.next(true)),
    switchMap((config) =>
      this.getStopChecks(config).pipe(
        catchError((error) => {
          this.error$.next(error);
          this.loading$.next(false);
          return EMPTY;
        }),
        tap(() => {
          this.loading$.next(false);
        })
      )
    ),
    shareReplay({ bufferSize: 1, refCount: true })
  );

  private readonly totalItems$ = this.stopChecksData$.pipe(
    map((res) => res.totalStopChecks)
  );

  readonly error$ = new Subject<HttpErrorResponse>();
  readonly loading$ = new BehaviorSubject<boolean>(true);
  readonly getData$ = this.stopChecksData$.pipe(
    map((res) => res.stopChecks),
    withLatestFrom(this.preferences.paginationType$),
    scan<[StopCheck[], LoadMoreMethod], StopCheck[]>(
      (acc, [data, paginationType]) => {
        if (paginationType !== LoadMoreMethod.PAGINATION) {
          return acc.concat(data);
        }

        return data;
      },
      []
    )
  );

  readonly paginationConfig$ = combineLatest([
    this.currentPage$,
    this.totalItems$.pipe(startWith(undefined)),
    this.preferences.maxNavPages$,
    this.preferences.pageSize$,
    this.preferences.paginationType$,
  ]).pipe(
    map(([currentPage, totalItems, maxNavPages, size, paginationType]) => ({
      currentPage,
      totalItems,
      maxNavPages,
      size,
      paginationType,
    }))
  );

  constructor(
    private readonly stopChecksDataHttpService: StopChecksHttpService,
    private readonly preferences: StopChecksListPropertiesService
  ) {}

  private getStopChecks(
    params: GetStopChecksRequestParams
  ): Observable<StopChecksGetResponse> {
    return this.stopChecksDataHttpService
      .getStopChecks(params, 'response')
      .pipe(map(fromResponse));
  }

  loadMore(page: number) {
    this.currentPage$.next(page);
  }
}
