import { Component, Directive, OnDestroy, Optional } from '@angular/core';
import { BbTemplate, ItemModel } from '@backbase/foundation-ang/core';
import { LoadMoreMethod, StopCheck } from '@backbase/payment-orders-ang';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';
import { PaginationConfig } from '../helpers/constants';
import { StopChecksListService } from '../services/stop-checks-list.service';
import { StopChecksListCommunicationService } from '../services/stop-checks-list-communication.service';
import { LoadMoreParams } from '@backbase/ui-ang/load-button';

@Component({
  selector: 'bb-stop-checks-list-container',
  templateUrl: 'stop-checks-list-container.component.html',
})
export class StopChecksListContainerComponent implements OnDestroy {
  private readonly destroyed$ = new Subject();

  /**
   * Reference to instance of customizable component.
   */
  readonly hostRef = this;
  readonly loading$ = this.checkService.loading$;
  readonly paginationConfig$: Observable<PaginationConfig> =
    this.checkService.paginationConfig$;
  readonly checksList$ = this.checkService.getData$;

  error?: HttpErrorResponse;
  checksList?: Array<StopCheck>;
  selectedCheck?: StopCheck;
  openDetails = false;

  constructor(
    @Optional()
    private readonly communicationService: StopChecksListCommunicationService,
    @Optional() private readonly model: ItemModel,
    private readonly checkService: StopChecksListService
  ) {
    this.communicationService?.refreshList$.subscribe(() =>
      this.checkService.loadMore(0)
    );

    this.checkService.error$
      .pipe(takeUntil(this.destroyed$))
      .subscribe((val) => {
        this.error = val;
      });

    this.checksList$.pipe(takeUntil(this.destroyed$)).subscribe((res) => {
      this.checksList = res;
    });
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
  }

  onCheckItemClick(selectedCheckItem: StopCheck) {
    this.openDetails = true;
    this.selectedCheck = selectedCheckItem;
  }

  onCloseDetails() {
    this.openDetails = false;
  }

  onPageChange(page: number) {
    this.checkService.loadMore(page++);
  }

  onLoadMore(params: LoadMoreParams) {
    this.checkService.loadMore(params.from);
  }

  isPagination(paginationType: LoadMoreMethod): boolean {
    return paginationType === LoadMoreMethod.PAGINATION;
  }

  showListLoading(
    isLoading: boolean,
    { paginationType, currentPage }: PaginationConfig
  ): boolean {
    const isFirstPage = currentPage === 0;
    return isLoading && (this.isPagination(paginationType) || isFirstPage);
  }
}

@Directive({
  selector: '[bbStopChecksListContainerCustomizableDirective]',
})
export class StopChecksListContainerCustomizableDirective extends BbTemplate<
  StopChecksListContainerComponent,
  undefined
> {}
